html {
}
body {
  margin: 0;
  width: 125%;
  height: 125%;
  transform-origin: top left;
  transform: scale(80%);
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  display: flex;
  flex-direction: column;
  align-items: center;
  background-color: rgb(223, 223, 223);
  padding: 40px;
}

h2 {
  margin-top: 0;
}
.App {
  display: flex;
  flex-direction: column;
  align-items: center;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}
.main-header {
  display: flex;
  flex-direction: column;
  align-items: center;
}
.main-header h1 {
  margin: 10px;
}
#main-icon {
  width: 80px;
}
.togglePreview {
  display: flex;
  margin-left: 40px;
}
.togglePreview button {
  border: none;
  border-bottom: 3px solid transparent;
  font-size: x-large;
  margin-top: 20px;
  margin-left: 5px;
  padding: 8px;
  min-width: 100px;
  margin-bottom: 10px;
}
.togglePreview button.active {
  border-bottom: 3px solid teal;
  background-color: white;
}
.togglePreview button:hover {
  cursor: pointer;
}
.cv-body {
  padding: 30px;
  background-color: white;
  box-shadow: 1px 3px 5px rgba(84, 84, 84, 0.9);
  width: 80vw;
  max-width: 800px;
  min-height: 80vh;
  margin: 20px;
  margin-top: 0px;
}
.personal-info {
  border-bottom: 1px solid gray;
  display: flex;
  justify-content: space-between;
  padding: 20px;
}

.primaryInfo {
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  margin-left: 10px;
}
.name {
  display: flex;
  align-items: center;
}
.name h1 {
  margin-right: 10px;
}
.name h1:hover {
  box-shadow: 1px 3px 5px rgba(84, 84, 84, 0.3);
  cursor: pointer;
}
.nameForm input {
  font-size: 30px;
  font-weight: bold;
  max-width: 166px;
  height: 40px;
}
.role {
  max-width: 250px;
  margin-bottom: 10px;
  margin-top: -15px;
  color: teal;
}
.role:hover {
  cursor: pointer;
  box-shadow: 1px 3px 5px rgba(139, 89, 89, 0.3);
}
.aboutMe {
  margin-top: 20px;
  max-width: 450px;
  line-height: 1.5;
}
.aboutMe:hover {
  box-shadow: 1px 3px 5px rgba(84, 84, 84, 0.5);
  cursor: pointer;
}
.aboutMe-form {
  display: flex;
  flex-direction: column;
}
.contact {
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  align-items: flex-end;
}
.contactItem {
  display: flex;
  text-align: right;
  margin-bottom: 5px;
}
.contactItem form {
  text-align: right;
  max-width: 120px;
}
.contactItem input {
  max-width: 120px;
  text-align: right;
}

.contactItemName {
  width: 100%;
}
.contactItemName:hover {
  cursor: pointer;
  box-shadow: 1px 3px 5px rgba(84, 84, 84, 0.3);
}

.contactItem img {
  width: 20px;
  margin-left: 10px;
}
.work-experience {
  border-bottom: 1px solid gray;
  padding: 20px;
}
.work-experience.saved {
  border-bottom: none;
  margin-top: -30px;
}
.companyInfo {
  margin-bottom: 30px;
  border-left: 3px solid teal;
  padding-left: 10px;
}
.companyInfo:hover {
  box-shadow: 1px 3px 5px rgba(84, 84, 84, 0.3);
  cursor: pointer;
}
.companyPositionTitle {
  margin-bottom: -10px;
  color: teal;
}
.placeOfWork {
  display: flex;
  align-items: center;
}

.companyCity {
  margin-left: 5px;
  font-size: 15px;
  font-style: italic;
}
.companyDates {
  margin-top: -15px;
  font-size: 12px;
}
.work-experience-form {
  padding: 20px;
}
.work-experience h2 {
  margin-top: 0px;
}
.work-experience-form form {
  display: flex;
  flex-direction: column;
  max-width: 400px;
}
.work-experience input {
  margin: 10px;
}
.work-experience button {
  margin: 10px;
}

.responsibilities input {
  margin: 5px;
  margin-left: 10px;
  margin-right: 10px;
}
.education {
  border-bottom: 1px solid gray;
  padding: 20px;
}
.userEducation {
  padding: 20px;
  margin-top: -30px;
}
.schoolObject:hover {
  box-shadow: 1px 3px 5px rgba(84, 84, 84, 0.3);
  cursor: pointer;
}
.schoolObject {
  margin-bottom: 30px;
  border-left: 3px solid teal;
  padding-left: 20px;
}
.schoolInfo {
  margin-top: -30px;
  display: flex;
  align-items: center;
}
.degree{
  color:teal;
}
.schoolCity {
  margin-left: 5px;
  font-size: 15px;
  font-style: italic;
}
.dates {
  margin-top: -15px;
  font-size: 12px;
}
.education form {
  display: flex;
  flex-direction: column;
  max-width: 400px;
  padding: 10px;
  border-bottom: 1px solid gray;
}
.education input {
  margin: 5px;
}
.education button {
  margin-right: 5px;
  margin-top: 10px;
}

.skills {
  padding: 20px;
  border-bottom: 1px solid gray;
}
.skills h2 {
  margin-bottom: -3px;
}

.skills input {
  margin-top: 10px;
}

.skillsContainer {
  display: flex;
  flex-wrap: wrap;
}
.skill {
  border-radius: 4px;
  background-color: rgba(0, 128, 128, 0.891);
  color: white;
  margin: 7px;
  margin-top: 15px;
  padding: 4px;
}
.skill:hover {
  cursor: pointer;
}
.responsibilities {
  display: flex;
  flex-direction: column;
}
.preview *:hover {
  cursor: text;
  box-shadow: none;
}
.preview {
  border-bottom: none;
}
.preview button {
  display: none;
}
.preview .primaryInfo .name h1:hover {
  box-shadow: none;
  cursor: text;
}

.work-experience.preview {
  padding-bottom: 0px;
}
.education.preview {
  margin-top: -20px;
}
footer {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 96%;
  padding: 3px;
}
a {
  display: flex;
  align-items: center;
}
#githubIcon {
  width: 40px;
  margin-left: 10px;
}
